import React, { useState, useEffect } from 'react';
import { f7} from 'framework7-react';
import {
  Page,
  Navbar,
  NavLeft,
  NavTitle,
  NavTitleLarge,
  NavRight,
  Link,
  Toolbar,
  Views,
  View,
  Block,
  BlockTitle,
  BlockFooter,
  LoginScreenTitle,
  List,
  Tabs,
  Tab,
  ListItem,
  ListButton,
  ListInput,
  Fab,
  Icon,
  FabButton,
  FabButtons,
  Row,
  Col,
  Button
} from 'framework7-react';

const Navber = (props) => {

  return [Footer()]

}

const Footer = () => {


  // views
  return (
    <View>
        <Navbar sliding={false}>
            <NavTitle>goChop</NavTitle>
        </Navbar>
        {/* Views/Tabs container */}
        <Views tabs className="safe-areas">
          {/* Tabbar for switching views-tabs */}
          <Toolbar tabbar labels bottom style={{backgroundColor:"white"}}>
            <Link tabLink="#view-recom" tabLinkActive iconIos="f7:wand_stars" iconAurora="f7:wand_stars" iconMd="material:wand" text="Recommended" />
            <Link tabLink="#view-discover" iconIos="f7:search" iconAurora="f7:search" iconMd="material:view_list" text="Discover" />
            <Link tabLink="#view-order" iconIos="f7:cart" iconAurora="f7:cart" iconMd="material:settings" text="Orders" />
            <Link tabLink="#view-profile" iconIos="f7:person_circle" iconAurora="f7:person" iconMd="material:settings" text="Profile" />
          </Toolbar>

          {/* Your main view/tab, should have "view-main" class. It also has "tabActive" prop */}
          <View id="view-recom" main tab tabActive url="/discover/" />

          {/* Catalog View */}
          <View id="view-discover" name="catalog" tab url="/*" />

          {/* Settings View */}
          <View id="view-order" name="settings" tab url="/*" />

          {/* Settings View */}
          <View id="view-profile" name="profile" tab url="/*" />

        </Views>
    </View>
  )
}
export default Navber

