import React, { useState, useEffect } from 'react';
import { f7} from 'framework7-react';
import {
  Page,
  Navbar,
  NavLeft,
  NavTitle,
  NavTitleLarge,
  NavRight,
  Link,
  Toolbar,
  Block,
  BlockTitle,
  BlockFooter,
  LoginScreenTitle,
  List,
  ListItem,
  ListButton,
  ListInput,
  Fab,
  Icon,
  FabButton,
  FabButtons,
  Row,
  Col,
  Button
} from 'framework7-react';

const Signup = (props) => {

  return [SignupScreen()]

}

const SignupScreen = () => {

  // initializing states
  const [email, setEmail] = useState(''); 
  const [password, setPassword] = useState(''); 

  // functions
  function Click(params) {
    
    const app =f7;
    const router =f7.views.main.router;
    app.dialog.preloader('Logging In')

    

    setTimeout(() => {
      // Hide Preloader
      app.dialog.close();
      router.navigate('/nav/');
    }, 1000);
    
    
  }

  // views
  return (
    <Page name="signup" noToolbar style={{backgroundColor:"white"}}>
      <div style={{ backgroundImage: 'url(static/food-bg.jpg)',backgroundRepeat:"no-repeat",backgroundPosition: "center center",backgrounAttachment: "fixed", backgroundSize:"cover",padding:"1px 0 50% 0"}}>
        <p style={{fontSize:"45px", fontFamily:"Billabong",textAlign:"center",margin:"auto",paddingTop:"10px"}}>goChop</p>
      </div>
      <Row style={{padding:"10px 15px",fontFamily:"Sf"}}>
        <Col width="50">
          <Button tooltip="use facebook to login" tooltipTrigger="hover" fill style={{fontFamily:"Sf-medium"}} onClick={Click}><Icon size="18px" color="white"  ios="f7:logo_facebook" aurora="f7:logo_facebook" md="material:person" href="/nav/" >
            </Icon><span style={{textAlign:"center",fontSize:"11px"}}>Login with Facebook</span>
          </Button>
        </Col>
        <Col width="50">
          <Button color="red" tooltip="use facebook to login" tooltipTrigger="hover" fill style={{fontFamily:"Sf-medium"}} onClick={Click}><Icon size="18px" color="white"  ios="f7:logo_google" aurora="f7:logo_google" md="material:person" >
            </Icon><span style={{textAlign:"center",fontSize:"11px"}}>Login with Google</span>
          </Button>
        </Col>
        <Col width="100">
          <BlockTitle style={{color:"#d6cfcf",textAlign:"center"}}>OR</BlockTitle>
          <hr style={{margin:"0 33%",backgroundColor:"#d6cfcf"}}/>
        </Col>
        <Col width="100">
          <List style={{fontFamily:"Sf"}}>
            <ListInput
              label="Username"
              type="text"
              placeholder="Your Phone, Username or Email"
              clearButton
            />

            <ListInput
              label="Password"
              type="password"
              placeholder="Your Password"
              clearButton
            />
          </List>
        </Col>
        <Col width="100" >
          <Button style={{textAlign:"right",fontFamily:"Sf-medium"}}>Forgot Password?</Button>
        </Col>
        <br/>
        <br/>
        <Col width="100" >
          <Button fill style={{fontFamily:"Sf-medium"}} onClick={Click}>Log In</Button>
        </Col>
        <br/>
        <br/>
        <br/>
        <br/>
        <Col width="100" style={{textAlign:"center"}}>
          <BlockFooter>Don't have an account?<span><Link style={{fontFamily:"Sf-medium",marginLeft:"7px"}}>Sign up</Link></span></BlockFooter>
        </Col>
      </Row>
    </Page>
  )
}
export default Signup

